const Navbar = lazy(() => import('./Navbar'))
const Map = lazy(() => import('./Map'))

function Single(){
    let location = useLocation();
    // console.log(location.pathname);
    let single = []; 
        var s=location.pathname;
        single = s.split('/');
    var single_id = parseInt(single[2]);
    // console.log(single)
    const dispatch = useDispatch();
    const [products, setProducts] = useState();


    useEffect(() => {
        dispatch(fetchData({
            url:`tm/real-estate/${single_id}`,
            action:(response)=>{
                if(response.success){
                    console.log("get_single ",response)
                    // console.log(response.data.rows[0].real_estates_all[0])
                    setProducts(response.data.rows);
                    // console.log(products)
                } else {
                    console.log("error_data ",response);
                    }
                }
            }))
    }, []);



    const [sort, setSort] = useState("false");
    const ToggleSort = () => {
        setSort(!sort); 
       };
       const [filter, setFilter] = useState("false");
    const ToggleFilter = () => {
        setFilter(!filter); 
       };

       const flickityOptions = {
        pageDots: true,
        wrapAround: true,
        prevNextButtons:true,
        pageDots:true
    }
    const flickityOptions_sub = {
        // asNavFor: ".carousel1",
        cellAlign: 'left',
        contain: true,
        prevNextButtons:false,
        pageDots: false
    }
    const flickityOptions_single = {
        cellAlign: 'left',
        wrapAround: true,
        prevNextButtons:true,
        pageDots: false,
        contain: true

    }
    // console.log(products?.position[0]?.x)

    // console.log(products?.position[0]?.y)
       
    return(
        <main>
            <Navbar />
            <div className="main py-6 border-t bg-gray-100">
                <div className="main1">
                <p className="text-gray-600 text-sm">Недвижимость в Москве / Аренда</p>
                <p className="text-blue-600  flex items-center mt-4">
                    <p className="text-gray-600 mr-3">сегодня, 13:21</p> 
                <Icon size={18} icon={ic_visibility_twotone} className="text-blue-600 mx-3"/>
                446 просмотров</p>
                </div>
            </div>
            <div className="main py-6 bg-gray-100">
                <div className="main1 bg-white p-4 border rounded-sm">
                    <h1 className="text-2xl text-blue-600 font-medium">{products?.real_estate_name}</h1>
                    <h1 className="text-xl font-medium mt-3">{products?.location}</h1>
                    <p className="text-sm text-gray-500 my-3">+993 {products?.phone}</p>
                    <h1 className="text-xl font-medium mb-5">{products?.price} ₽</h1>
                    <div className="w-full flex justify-between">
                    <div className="slider2">
                            <Flickity
                            className={'carousel1'} // default ''
                            elementType={'div'} // default 'div'
                            options={flickityOptions} // takes flickity options {}
                            disableImagesLoaded={false} // default false
                            reloadOnUpdate // default false
                            static // default false
                            >
                                {products?.images?.map((img)=>(
                                        <div className="carousel1-cell">
                                            <img src={`http://192.168.31.30:2000/${img?.destination}-big.webp`} className="img" />
                                        </div>
                                    ))}
                                {/* <div className="carousel1-cell">
                                    <img src={image1} className="img" />
                                </div>
                                <div className="carousel1-cell">
                                    <img src={image2} className="img" />
                                </div>
                                <div className="carousel1-cell">
                                    <img src={image3} className="img" />
                                </div>
                                <div className="carousel1-cell">
                                    <img src={image3} className="img" />
                                </div>
                                <div className="carousel1-cell">
                                    <img src={image3} className="img" />
                                </div> */}
                            </Flickity>
                            <Flickity
                            className={'carousel-nav'} // default ''
                            elementType={'div'} // default 'div'
                            options={flickityOptions_sub} // takes flickity options {}
                            disableImagesLoaded={false} // default false
                            reloadOnUpdate // default false
                            static // default false
                            >
                                {products?.images?.map((img1)=>(
                                        <div className="carousel1_sub-cell">
                                            <img src={`http://192.168.31.30:2000/${img1?.destination}-big.webp`} className="img_sub" />
                                        </div>
                                    ))}
                                    
                            </Flickity>
                    </div>
                    <div className="map_location bg-blue-100">
                    <Map />
                    </div>
                    </div>
                        <div className="w-1/2 mt-8">
                        {/* <hr/> */}
                        <p className="text-sm text-gray-500 mt-5">{products?.description}</p>
                        </div>
                    </div>
                </div>
                {/* <div className="main py-6 bg-gray-100">
                    <div className="main1 bg-white p-4 border rounded-sm">
                        <h1 className="text-lg font-medium mb-5">Рекомендации по истории поиска</h1>
                    <Flickity
                            className={'carousel_s'} // default ''
                            elementType={'div'} // default 'div'
                            options={flickityOptions_single} // takes flickity options {}
                            disableImagesLoaded={false} // default false
                            reloadOnUpdate // default false
                            static // default false
                            >
                                <div className="carousel_single border rounded-sm">
                                    <img src={image1} className="img_single" />
                                    <h1 className="px-2 p-1">slider</h1>
                                </div>
                                <div className="carousel_single border rounded-sm">
                                    <img src={image2} className="img_single" />
                                </div>
                                <div className="carousel_single border rounded-sm">
                                    <img src={image3} className="img_single" />
                                </div>
                                <div className="carousel_single border rounded-sm">
                                    <img src={image3} className="img_single" />
                                </div>
                                <div className="carousel_single border rounded-sm">
                                    <img src={image3} className="img_single" />
                                </div>
                            </Flickity>
                    </div>
                </div> */}

        </main>

    )
}

export default Single;